footer {
  padding-bottom: 20px;
  position: absolute;
  width: 100%;
  bottom: 0;
}
.footer-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer-left .logo a {
  text-decoration: none;
  color: var(--text-color-primary);
  font-family: var(--font-utm-avo-bold);
  font-size: 21px;
}
.footer-left .logo a span {
  color: var(--text-color-second);
}
.footer-left .logo img {
  margin-right: 3px;
}
.footer-nav {
  display: flex;
  justify-content: flex-end;
}
.footer-nav-item {
  padding: 0 15px;
  display: flex;
  align-items: center;
}
.footer-nav-item a {
  color: var(--label-color-primary);
  text-decoration: none;
  font-size: 16px;
}
.footer-nav-item a:hover {
  color: var(--text-color-second);
}
.footer-nav-item:last-child {
  padding-right: 0;
}
.footer-bottom {
  color: var(--label-color-primary);
  font-size: 16px;
  margin-top: 15px;
}
.footer-right {
  display: flex;
}